import { Injectable } from '@angular/core';
import { Project } from './project';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class ProjectsService {

  public projects: Observable<Project[]>;
  private baseUrl = './api/project';
//  private baseUrl = 'http://localhost:8080/api2/project';

  constructor(private http: HttpClient) {
    //this.projects = this.http.get(this.baseUrl+"s") as Observable<Project[]>;
  }

  getProjects() : Observable<Project[]>{
//    return this.projects;
      return this.http.get(this.baseUrl+"s") as Observable<Project[]>;
  }

  getProjectById(id : string): Observable<Project> {
    return this.http.get(`${this.baseUrl}/${id}`) as Observable<Project>;
  }

  addProject(project: Project): Observable<Project> {
      return this.http.post(this.baseUrl, project) as Observable<Project>;
  }

  deleteProjectById(id: string) : Observable<any> {
    return this.http.delete(`${this.baseUrl}/${id}`)  as Observable<Project>;
  }

  updateProject(project : Project): Observable<Project> {
    return this.http.put(`${this.baseUrl}/${project.id}`, project) as Observable<Project>;
  }

}
