import { Component, OnInit, ViewChild } from '@angular/core';
import { Project } from './project';
import { Subscription, Observable } from 'rxjs';
import { NgForm } from '@angular/forms';
import { GridApi, GridOptions } from 'ag-grid';
import { ProjectsService } from './projects.service';
import { Router } from '@angular/router';
import { MatEditButtonGridRenderComponent } from '../grid-custom-components/mat-edit-button-grid-render/mat-edit-button-grid-render.component';
import { MatRemoveButtonGridRenderComponent } from '../grid-custom-components/mat-remove-button-grid-render/mat-remove-button-grid-render.component';

@Component({
  selector: 'app-projects',
  templateUrl: './projects.component.html',
  styleUrls: ['./projects.component.scss']
})
export class ProjectsComponent implements OnInit {

  public project = new Project();
//  public projectSelected = new Project();
  public context;
  private projectSub: Subscription;
  
  @ViewChild('myForm',null) myForm : NgForm;
  
  private gridApi: GridApi;
  private gridColumnApi;
  private columnDefs: any;
  private rowData: Project[];
  private gridOptions: GridOptions;

  constructor(private projectsService: ProjectsService, private router : Router) {

    this.context = { componentParent: this };

    this.gridOptions = <GridOptions>{};
    this.gridOptions.enableFilter = true;
    this.gridOptions.enableSorting = true;
    this.gridOptions.rowSelection = 'single';

    this.columnDefs = [
      { headerName: 'Id', field: 'id', hide: true },
      { headerName: 'Code', field: 'code', filter: 'text', width: 120 },
      { headerName: 'Name', field: 'name', filter: 'text', width: 250 },
      { headerName: 'Start Date', field: 'startDate', filter: 'date', width: 140 },
      { headerName: 'Spring Days', field: 'springDays', filter: 'number',width: 150, valueFormatter: this.currencyFormatter },
      { headerName: 'Status', field: 'status', filter: 'text', width: 120 }
    ];
  }

  ngOnInit(): void {
    this.rowData = [];
    this.projectSub = this.projects.subscribe(
      projectList => this.rowData = projectList
    );
  }

  currencyFormatter(params: any) {
    return '£' + params.value;
  }

  dateFormatter(params: any) {
    return '£' + params.value;
  }


  ngOnDestroy(): void {
    if (this.projectSub) {
      this.projectSub.unsubscribe();
    }
  }

  get projects(): Observable<Project[]> {
    return this.projectsService.getProjects();
  }
  
  getProjectByKey(id: string): Observable<Project> {
    return this.projectsService.getProjectById(id);
  }

  removeProject(id: string) {
    this.projectsService.deleteProjectById(id);
  }

  editProject(data: any) {
    this.project = new Project(data);
  }
  
  addProject(): Observable<Project> {
    this.project.status = "OPEN";
    let addedItem = this.projectsService.addProject(this.project);
    this.initialMode();
    return addedItem;
  }

  updateProject(){
    this.projectsService.updateProject(this.project);
    this.initialMode();
  }

  resetControls() {
    Object.keys(this.myForm.controls).forEach(field => {      
      const control = this.myForm.control.get(field);         
      control.markAsUntouched();                              
    });
  }

  // Call from MatRemoveButtonGridRenderComponent
  removeFromComponent(id: string){
    this.removeProject(id);
    this.initialMode();
  }

  // Call from MatEditButtonGridRenderComponent
  editFromComponent(data: any){
    this.editProject(data);
  }

  cancelEditMode() {
    this.initialMode();
  }  

  private initialMode() {
    this.project = new Project();
//    this.resetControls();
  }

  isEditMode() {
    return this.project.id;
  }

  onGridReady(params: any) {
    this.gridApi = params.api;
    this.gridColumnApi = params.columnApi;
    this.gridColumnApi.autoSizeColumns();
  }

  onSelectionChanged() {
    var selectedRows = this.gridApi.getSelectedRows();
    var selectedRowAux: any;
    selectedRows.forEach(function(row, index) {
//      if (index !== 0) {
//        selectedRowsString += ", ";
//      }
        selectedRowAux = row;
    });
//    this.loginService.setProjectSelected(selectedRowAux);
//    if (!this.loginService.currentSpring) {
//      this.router.navigate(['/springs']);
//    }
//    this.projectSelected = selectedRowAux;
//    document.querySelector("#selectedRows").innerHTML = selectedRowsString;
  }  
}
